/*
* @subsection   Isotope
*/

$isotope-masonry-item-offset: 15px;
$isotope-masonry-item-offset-lg: 55px;
$isotope-masonry-item-offset-xl: 60px;
html .page {

  [data-isotope-layout] {
    position: relative;
    @include transform(translateY(-$isotope-masonry-item-offset));
    display: block;
    margin-bottom: -$isotope-masonry-item-offset;
    transition: .4s all ease;
    min-height: 160px;

    &.isotope-lg {
      @include transform(translateY(-$isotope-masonry-item-offset-lg));
      margin-bottom: -$isotope-masonry-item-offset-lg;
      &:after {
        margin-top: $isotope-masonry-item-offset-lg / 2;
      }
      [class*="col-"] {
        margin-top: $isotope-masonry-item-offset-lg;
      }
    }

    &.isotope-xl {
      @include transform(translateY(-$isotope-masonry-item-offset-xl));
      margin-bottom: -$isotope-masonry-item-offset-xl;
      &:after {
        margin-top: $isotope-masonry-item-offset-xl / 2;
      }
      [class*="col-"] {
        margin-top: $isotope-masonry-item-offset-xl;
      }
    }

    &:after {
      content: '';
      position: absolute;
      margin-top: $isotope-masonry-item-offset / 2;
      width: 64px;
      height: 64px;
      top: 50%;
      left: 50%;
      background-image: url('data:image/png;base64,');
      background-position: -1152px 0;
      animation: 0.7s sprite-animation steps(18) infinite;
      transition: .4s all ease;
      @include transform(translate(-50%, -50%));
    }

    [class*="col-"] {
      display: block;
      font-size: 0;
      margin-top: $isotope-masonry-item-offset;
      opacity: 0;
      transition: .4s opacity ease;
      p, ul, li {
        font-size: $font-size-base;
        max-width: 100%;
      }

      /*
       * @bugfix item position after filtering
       * @affected iOS
      */
      //width: 100%;
    }

    &.isotope--loaded {
      [class*="col-"] {
        opacity: 1;
      }

      &:after {
        opacity: 0;
        visibility: hidden;
      }
    }
  }

  .isotope-filters {
    position: relative;
    z-index: 100;
    margin-bottom: 47px;
    margin-top: 47px;
    &-lg {
      @media (min-width: $screen-lg-min) {
        margin-bottom: 75px;
        margin-top: 75px;
      }
    }
    > * {
      margin-top: 0;
      display: inline-block;
      vertical-align: middle;
    }

    .list-isotop {
      > li {
        display: inline-block;
        > a {
          color: $gray-darker;
          @media (min-width: $screen-md-min) {
            font-size: 24px;
          }
          &:hover,
          &.active {
            color: $brand-primary;
          }
        }
        & + li {
          @media (max-width: $screen-md-min - 1) {
            margin-left: 15px;
          }
          @media (min-width: $screen-md-min) {
            &:before {
              content: '/';
              font-size: 24px;
              margin-left: 36px;
              margin-right: 36px;
              color: $gray-lighter;
            }
          }
        }
      }

    }

    @media (min-width: $screen-md-min) {
      .isotope-filters-trigger {
        display: none;
      }
    }
  }

}
