.ui-to-top {
  width: 50px;
  height: 50px;
  font-size: 24px;
  line-height: 46px;
  position: fixed;
  right: 15px;
  bottom: 15px;
  overflow: hidden;
  text-align: center;
  text-decoration: none;
  z-index: 200;
  transition: .3s all ease;

  @include transform(translateY(100px));

  &, &:focus, &:active {
    color: #FFF;
    background: $brand-primary;
  }

  &:hover {
    color: $white;
    background: $brand-shamrock;
    text-decoration: none;
  }

  &.active{
    @include transform(translateY(0));
  }
}

.mobile .ui-to-top,
.tablet .ui-to-top {
  display: none !important;
}

@media (min-width: $screen-xs-min){
  .ui-to-top {
    right: 40px;
    bottom: 40px;
  }
}


